"use client"

import * as React from "react"
import QRCode from "qrcode"
import { DashboardLayout } from "@/components/dashboard/layout"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Slider } from "@/components/ui/slider"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Plus, Download, Save, Palette, QrCode, Sparkles } from 'lucide-react'
import Image from "next/image"

interface QRTemplate {
  id: string
  name: string
  style: "standard" | "dots" | "rounded" | "extra-rounded" | "classy" | "classy-rounded"
  size: number
  fgColor: string
  bgColor: string
  errorCorrection: "L" | "M" | "Q" | "H"
  logo?: string
}

const STYLE_PRESETS = {
  standard: { title: "Standard", icon: <QrCode className="w-4 h-4" /> },
  dots: { title: "Dots", icon: <Sparkles className="w-4 h-4" /> },
  rounded: { title: "Rounded", icon: <div className="w-4 h-4 rounded-full bg-primary" /> },
  "extra-rounded": { title: "Smooth", icon: <div className="w-4 h-4 rounded-full bg-gradient-to-br from-primary to-purple-600" /> },
  classy: { title: "Classy", icon: <div className="w-4 h-4 border-2 border-primary rounded" /> },
  "classy-rounded": { title: "Elegant", icon: <div className="w-4 h-4 rounded-lg bg-primary/20 border-2 border-primary" /> },
}

export default function QRCodesPage() {
  const canvasRef = React.useRef<HTMLCanvasElement>(null)

  const [templates, setTemplates] = React.useState<QRTemplate[]>([
    { id: "1", name: "Classic Pro", style: "standard", size: 320, fgColor: "#000000", bgColor: "#ffffff", errorCorrection: "H" },
    { id: "2", name: "Brand Blue Dots", style: "dots", size: 340, fgColor: "#1d4ed8", bgColor: "#ffffff", errorCorrection: "H" },
    { id: "3", name: "Modern Green", style: "rounded", size: 300, fgColor: "#16a34a", bgColor: "#f0fdf4", errorCorrection: "Q" },
  ])

  const [activeTemplateId, setActiveTemplateId] = React.useState(templates[0].id)
  const [previewUrl, setPreviewUrl] = React.useState("https://yourstore.com/product/123")
  const [logoImage, setLogoImage] = React.useState<string | null>(null)

  const activeTemplate = templates.find(t => t.id === activeTemplateId) || templates[0]
  const [edit, setEdit] = React.useState<QRTemplate>({ ...activeTemplate })

  // Sync edit state when switching templates
  React.useEffect(() => {
    setEdit({ ...activeTemplate })
    setLogoImage(activeTemplate.logo || null)
  }, [activeTemplateId])

  // Generate QR Code with advanced styling
  const generateQR = React.useCallback(async () => {
    if (!canvasRef.current) return

    const opts: any = {
      text: previewUrl || "https://example.com",
      width: edit.size,
      height: edit.size,
      colorDark: edit.fgColor,
      colorLight: edit.bgColor,
      correctLevel: {
        L: QRCode.CorrectLevel.L,
        M: QRCode.CorrectLevel.M,
        Q: QRCode.CorrectLevel.Q,
        H: QRCode.CorrectLevel.H,
      }[edit.errorCorrection],

      // Advanced Styling Based on Selected Tab
      dotScale: edit.style === "dots" ? 0.8 : 1,
      quietZone: 20,

      // Logo
      logo: logoImage || undefined,
      logoWidth: edit.size * 0.2,
      logoHeight: edit.size * 0.2,
      logoBackgroundColor: edit.bgColor,
      logoRadius: 8,

      // Styling per type
      PO: edit.style.includes("classy") ? edit.fgColor : undefined, // Corner squares
      PI: edit.style.includes("classy") ? edit.fgColor : undefined,
      PO_TL: edit.style === "classy-rounded" ? edit.fgColor : undefined,
      PI_TL: edit.style === "classy-rounded" ? edit.fgColor : undefined,

      // Rounded corners
      cornerRadius:
        edit.style === "rounded" ? 0.3 :
        edit.style === "extra-rounded" ? 0.6 :
        edit.style === "classy-rounded" ? 0.5 : 0,
    }

    try {
      await QRCode.toCanvas(canvasRef.current, previewUrl || "https://example.com", opts)
    } catch (err) {
      console.error(err)
    }
  }, [previewUrl, edit, logoImage])

  React.useEffect(() => {
    generateQR()
  }, [generateQR])

  const saveTemplate = () => {
    setTemplates(prev => prev.map(t => t.id === activeTemplateId ? { ...edit, logo: logoImage || undefined } : t))
  }

  const downloadQR = () => {
    if (!canvasRef.current) return
    const link = document.createElement("a")
    link.download = `qr-${edit.name.toLowerCase().replace(/\s+/g, "-")}.png`
    link.href = canvasRef.current.toDataURL("image/png")
    link.click()
  }

  const handleLogoUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      const reader = new FileReader()
      reader.onload = () => setLogoImage(reader.result as string)
      reader.readAsDataURL(file)
    }
  }

  return (
    <DashboardLayout>
      <div className="max-w-7xl mx-auto py-8">
        <div className="flex items-center justify-between mb-8">
          <div>
            <h1 className="text-3xl font-bold flex items-center gap-3">
              <Palette className="h-8 w-8 text-primary" />
              QR Code Designer Pro
            </h1>
            <p className="text-muted-foreground mt-1">Beautiful, scannable QR codes  instantly customizable</p>
          </div>
          <div className="flex gap-3">
            <Button onClick={saveTemplate} size="lg">
              <Save className="h-5 w-5 mr-2" />
              Save Template
            </Button>
            <Button onClick={downloadQR} size="lg">
              <Download className="h-5 w-5 mr-2" />
              Download PNG
            </Button>
          </div>
        </div>

        <div className="grid lg:grid-cols-2 gap-10">
          {/* Left: Controls */}
          <div className="space-y-6">
            {/* Template Selector */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Saved Templates</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-3 gap-3">
                  {templates.map((t) => (
                    <button
                      key={t.id}
                      onClick={() => setActiveTemplateId(t.id)}
                      className={`p-4 rounded-lg border-2 transition-all ${
                        activeTemplateId === t.id
                          ? "border-primary bg-primary/5 shadow-lg"
                          : "border-border hover:border-primary/50"
                      }`}
                    >
                      <div className="bg-white rounded p-3 mb-2 flex justify-center">
                        <canvas width={60} height={60} className="w-full h-full" />
                      </div>
                      <p className="text-xs font-medium truncate">{t.name}</p>
                    </button>
                  ))}
                  <button className="p-4 rounded-lg border-2 border-dashed border-muted-foreground/50 hover:border-primary/50 transition-all flex flex-col items-center justify-center gap-2 text-muted-foreground">
                    <Plus className="w-5 h-5" />
                    <span className="text-xs">New</span>
                  </button>
                </div>
              </CardContent>
            </Card>

            {/* Style Tabs */}
            <Card>
              <CardHeader>
                <CardTitle>Design Style</CardTitle>
              </CardHeader>
              <CardContent>
                <Tabs value={edit.style} onValueChange={(v) => setEdit({ ...edit, style: v as any })}>
                  <TabsList className="grid grid-cols-3 lg:grid-cols-6 w-full">
                    {Object.entries(STYLE_PRESETS).map(([key, { title, icon }]) => (
                      <TabsTrigger key={key} value={key} className="flex items-center gap-2">
                        {icon}
                        <span className="hidden sm:inline">{title}</span>
                      </TabsTrigger>
                    ))}
                  </TabsList>
                </Tabs>
              </CardContent>
            </Card>

            {/* Customization */}
            <Card>
              <CardHeader>
                <CardTitle>Customize</CardTitle>
              </CardHeader>
              <CardContent className="space-y-6">
                <div>
                  <Label>URL or Text</Label>
                  <Input
                    value={previewUrl}
                    onChange={(e) => setPreviewUrl(e.target.value)}
                    placeholder="https://yourstore.com/product/123"
                    className="mt-2"
                  />
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label>Error Correction</Label>
                    <Select value={edit.errorCorrection} onValueChange={(v) => setEdit({ ...edit, errorCorrection: v as any })}>
                      <SelectTrigger className="mt-2">
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="L">Low (7%)</SelectItem>
                        <SelectItem value="M">Medium (15%)</SelectItem>
                        <SelectItem value="Q">High (25%)</SelectItem>
                        <SelectItem value="H">Highest (30%)</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>

                  <div>
                    <Label>Size: {edit.size}px</Label>
                    <Slider
                      value={[edit.size]}
                      onValueChange={([v]) => setEdit({ ...edit, size: v })}
                      min={200}
                      max={600}
                      step={20}
                      className="mt-3"
                    />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-6">
                  <div>
                    <Label>Color</Label>
                    <div className="flex gap-3 mt-2">
                      <Input type="color" value={edit.fgColor} onChange={(e) => setEdit({ ...edit, fgColor: e.target.value })} className="w-24 h-12" />
                      <Input value={edit.fgColor} readOnly className="flex-1" />
                    </div>
                  </div>
                  <div>
                    <Label>Background</Label>
                    <div className="flex gap-3 mt-2">
                      <Input type="color" value={edit.bgColor} onChange={(e) => setEdit({ ...edit, bgColor: e.target.value })} className="w-24 h-12" />
                      <Input value={edit.bgColor} readOnly className="flex-1" />
                    </div>
                  </div>
                </div>

                <div>
                  <Label>Add Logo (Optional)</Label>
                  <Input type="file" accept="image/*" className="mt-2" onChange={handleLogoUpload} />
                  {logoImage && (
                    <div className="mt-3">
                      <Image src={logoImage} alt="Logo" width={100} height={100} className="rounded-lg border" />
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Right: Live Preview */}
          <div className="flex flex-col items-center justify-center">
            <Card className="w-full max-w-2xl shadow-2xl">
              <CardHeader className="text-center">
                <CardTitle className="text-2xl">Live Preview</CardTitle>
                <p className="text-sm text-muted-foreground">High-quality, scannable QR code</p>
              </CardHeader>
              <CardContent className="flex flex-col items-center space-y-8 pb-10">
                <div
                  className="p-12 rounded-3xl shadow-2xl border-4 border-border/50"
                  style={{ backgroundColor: edit.bgColor }}
                >
                  <canvas ref={canvasRef} className="rounded-xl shadow-xl" />
                </div>

                <div className="text-center space-y-2">
                  <p className="font-bold text-xl">{edit.name || "Custom QR Code"}</p>
                  <p className="text-sm text-muted-foreground">
                    {edit.size}px  {edit.errorCorrection} Error Correction  {STYLE_PRESETS[edit.style].title}
                  </p>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </DashboardLayout>
  )
}